@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Old+Standard+TT:ital@1&display=swap);
/* fonts */

/* custom properties */
:root{
  --off-white: #f2f2f2;
  --black: rgba(0,0,16,0.8);
  --blue: #0365c0;
  --lightblue: rgb(107, 233, 255);
  --green: #00cc4f;
  --orange: #ffd152; /* #ffb144; */
  --disabled: rgba(0,0,16,.3);
  --shadow: 0 0.5rem 1rem rgba(0, 0, 16, 0.19),
    0 0.3rem 0.3rem rgba(0, 0, 16, 0.23);
}

/* resets */
body, html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
  background-color: var(--off-white);
  font-family: 'Lato', sans-serif;
  color: rgba(0,0,16,0.8);
  color: var(--black);
  box-sizing: border-box;
}

header {
  padding: 1rem 2rem;
  background: #0365c0;
  background: var(--blue);
  color: #f2f2f2;
  color: var(--off-white);
  font-size: 2rem;
  font-weight: bold;
}

.container {
  padding: 1rem 2rem;
}

.filters{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  margin-bottom: 1rem;
}

.card {
  display: flex;
  background-color: white;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 16, 0.19),
    0 0.3rem 0.3rem rgba(0, 0, 16, 0.23);
  box-shadow: var(--shadow);
}

.filter-card {
  padding: 0.8rem;
}

.filter-container {
  padding: 0.4rem 0.8rem;
}

.filter-text p {
  color: rgba(0,0,16,0.8);
  color: var(--black);
  font-size: 1.3 rem;
  padding: 0.3rem;
}

.filter{
  background-color: rgba(0,0,16,0.8);
  background-color: var(--black);
  padding: 0.4rem 0.4rem 0.1rem 0.4rem;
  border: 3px solid #0365c0;
  border: 3px solid var(--blue);
  border-radius: 2.2rem;
}

.filter-form{
  display: flex;
  align-items: center;
}

.filter-all + span {
  font-size: 2rem;
  padding: 0.5rem 1rem;
  color: #f2f2f2;
  color: var(--off-white);
}

svg{
  fill: #f2f2f2;
  fill: var(--off-white);
  height: 2rem;
  width: 2rem;
  margin: .5rem;
}

input[type="radio"]{
  position: absolute;
  opacity: 0;
}

input + span,
input + svg {
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-all:hover,
.filter-all:checked + span,
.filter-all:focus + span {
  color: #0365c0;
  color: var(--blue);
}

.beer:hover + svg,
.beer:checked + svg,
.beer:focus + svg {
  fill: #ffd152;
  fill: var(--orange);
}

.hops:hover + svg,
.hops:checked + svg,
.hops:focus + svg {
  fill: #00cc4f;
  fill: var(--green);
}

.pageBtn:hover {
  fill: rgb(107, 233, 255);
  fill: var(--lightblue);
  cursor: pointer;
}

.disabled {
  fill: rgba(0,0,16,.3);
  fill: var(--disabled);
  cursor: default;
}

.beers {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 1rem;
}

.beer-wrapper{
  justify-content: center;
  align-items: flex-end;
  position: relative;
}

.beers .beer {
  display: grid;
  grid-auto-columns: 1fr;
  align-items: end;
  padding: 2rem;
  text-align: center;
  width: 100%;
}

.beer .beer__img {
  margin: auto;
  height: 20rem;
}

.beer .beer__info {
  display: flex;
  justify-content: space-between;
}
.beer__content{
  position: absolute;
  top: 0;
  left: 0;
  padding: 1.5rem;
  width: calc(100% - (1.5rem * 2));
  height: calc(100% - (1.5rem * 2));
  background: white;
  overflow: auto;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.8s ease;
}
.beer__content:hover {
  opacity: 0.92;
  cursor: default;
}
.beer__name {
  margin: 0 auto .5rem auto;
  font-family: "Old Standard TT", serif;
  font-size: 2.4rem;
  line-height: 1.07em;
  letter-spacing: -0.015em;
}
.beer__tagline {
  padding-bottom: .5rem;
  font-family: "Old Standard TT", serif;
  font-style: italic;
  border-bottom: 2px solid #0365c0;
  border-bottom: 2px solid var(--blue);
}
.beer__description {
  margin: 1rem 0;
  font-size: .9rem;
  line-height: 1.3rem;
}
.beer__food-pairing {
  font-style: italic;
  font-size: .9rem;
  line-height: 1.3em;
}
